import { DidKeyPair, DidObject } from 'dw-did-sdk';

class DidModel extends DidObject {
  public id: string;

  constructor(
    scheme: string,
    method: string,
    didSuffix: string,
    longFormSuffixData: string,
    signingKeyId: string,
    published: boolean,
    keys: {
      signing: DidKeyPair;
      update?: DidKeyPair;
      recovery?: DidKeyPair;
    }
  ) {
    super(scheme, method, didSuffix, longFormSuffixData, signingKeyId, published, keys);
    this.id = DidModel.ID;
  }

  static ID = 'onlyid';

  static createByDidObject(didObject: DidObject) {
    return new DidModel(
      didObject.scheme,
      didObject.method,
      didObject.didSuffix,
      didObject.longFormSuffixData,
      didObject.signingKeyId,
      didObject.published,
      didObject.keys
    );
  }
}

export default DidModel;
